.message-page {
    user-select: none;
    font-family: "Poppins", sans-serif;
    position: relative;
    background: url("../static/landing.jpg");
    background-position-y: 90%;
    height: clamp(500px, 100vh, 100vh);
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    .darken {
        position: absolute;
        width: 100%;
        height: 100%;
        min-height: 100vh;
        background-color: rgba(0, 0, 0, 0.6);
    }
    .container {
        height: 100%;
        .main-message {
            position: relative;
            display: flex;
            flex-direction: column;
            height: 100%;
            justify-content: center;
            color: black;
            .message {
                animation: fadeUp 0.5s ease forwards;
                max-width: 200px;
                margin: 0 auto;
                background-color: white;
                box-shadow: 0px 5px 10px 1px rgba(255,255,255,1);
                padding: 0.2rem 1rem 0.2rem 1rem;
                border: 1px solid black;
                border-radius: 5px;
                min-width: 300px;
                text-align: center;
                h3 {
                    font-size: 24px;
                }
                .inputs {
                    .title {
                        font-size: 14px;
                        word-break: break-all;
                    }
                    
                    form {
                        display: flex;
                        flex-direction: column;
                        .message-input {
                            width: 100%;
                            min-height: 2rem;
                            margin-top: 0.5rem;
                            border-radius: 5px;
                            border: 1px solid black;
                        }
                        .submit-btn {
                            font-family: 'Poppins', sans-serif;
                            border: 1px solid black;
                            color: white;
                            background-color: #2ecc71;
                            margin-top: 1rem;
                            border-radius: 5px;
                            padding: 0.4rem 0 0.4rem;
                            transition: 0.3s background-color ease;
                        }
                        .submit-btn:hover {
                            background-color: #5e41a7;
                        }
                    }
                }
                .onSuccess {
                    display: flex;
                    flex-direction: column;
                    .send-anot {
                        font-size: 12px;
                        font-style: italic;
                        text-decoration: underline;
                        cursor: pointer;
                    }
                }
                .join {
                    display: block;
                    padding-top: 1rem;
                    cursor: pointer;
                    font-size: 12px;
                    text-decoration: underline;
                }
            }
        }
    }
}
