.dash-page {
    user-select: none;
    font-family: "Poppins", sans-serif;
    position: relative;
    background: url("../static/landing.jpg");
    background-position-y: 90%;
    min-height: 100vh;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    .darken {
        position: absolute;
        width: 100%;
        height: 100%;
        min-height: 100vh;
        background-color: rgba(0, 0, 0, 0.6);
    }
    .container {
        .main-dashboard {
            animation: fadeUp 0.5s ease forwards;
            padding-top: 7rem;
            padding-bottom: 5rem;
            position: relative;
            display: flex;
            flex-direction: column;
            color: white;
            .dash-title {
                h1 {
                    font-size: 65px;
                }
                
            }
            .your-sharable-link {
                margin-top: 2rem;
                span {
                    user-select: all;
                }
            }
            .messages {
                margin-top: 3rem;
                h1 {
                    font-size: 35px;
                    padding: 0 0 1rem 0;
                }
                .messages-main {
                    display: grid;
                    width: 100%;
                    grid-template-columns: 1fr 1fr 1fr 1fr;
                    gap: 20px;
                    .message {
                        padding: 1rem;
                        box-shadow: 0px 2px 3px 1px rgba(255,255,255,1);
                        background-color: #353b48;
                        border-radius: 5px;
                        h1 {
                            text-align: center;
                        }
                        span {
                            display: block;
                            text-align: center;
                            word-break: break-all;
                        }
                    }
                }
            }
        }
    }
}
