.wrap-form {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    animation: fadeUp 0.5s ease forwards;
    form {
        background-color: rgba(255,255,255,1);
        box-shadow: 0px 5px 10px 1px rgba(255,255,255,1);
        border-radius: 15px;
        min-width: 300px;
        max-width: 500px;
        width: 100%;
        padding: 2rem;
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        label {
            font-size: 13px;
        }
        input {
            margin-bottom: 0.2rem;
        }
        .submit-btn {
            font-family: 'Poppins', sans-serif;
            border: 1px solid black;
            color: white;
            background-color: #2ecc71;
            margin-top: 2rem;
            border-radius: 5px;
            padding: 0.4rem 0 0.4rem;
            transition: 0.3s background-color ease;
        }
        .submit-btn:hover {
            background-color: #5e41a7;
        }
        span {
            &:nth-of-type(1) {
                i {
                    font-size: 12px;
                    text-decoration: underline;
                }
            }
            &:nth-of-type(2) {
                margin-top: 0.2rem;
                text-decoration: underline;
            }
        }
        .message-response {
            color: white;
            border-radius: 5px;
            span {
                display: block;
                font-size: 12px;
                text-align: center;
            }
        }
    }
}
