@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@800&family=Poppins:wght@400;600;700&display=swap");
@import "./sass/homepage";
@import "./sass/login-register";
@import "./sass/dashboard";
@import "./sass/message";
@import "./sass/media-queries";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    margin: 0 auto;
    padding: 0 1rem 0 1rem;
}

.nav-bar {
    animation : fadeDown 0.5s cubic-bezier(0,.24,.32,1) forwards;
    .container {
        .burger-menu-btn {
            display: none;
            padding: 0.5rem;
            border-radius: 5px;
            i {
                font-weight: 700;
                font-size: 2rem;
                color: white;
            }
        }
    }
}

.mobile-nav {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 99999;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 1);
    color: white;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    overflow: hidden;
    transition: 0.4s transform cubic-bezier(0,.24,.32,1);
    padding: 30px 30px 10px 30px;
    .close-button-wrap {
        color: red;
        display: flex;
        width: 100%;
        justify-content: flex-end;
        i {
            font-size: 25px;
        }
    }
    .choices {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        width: 100%;
        span {
            margin-top: 2rem;
            a {
                color: white;
                text-decoration: none;
                font-family: "Poppins", sans-serif;
                font-size: 20px;
            }
        }
    }
    img {
        width: 70px;
        margin-top: 3rem;
    }
    .mark {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        justify-content: flex-end;
        padding-bottom: 2rem;
        height: 100%;
        font-size: 10px;
        user-select: none;
        font-weight: lighter;
        color: rgb(88, 88, 88);
    }
}

@keyframes fadeUp {
    from {
        opacity : 0;
        transform : translateY(2rem);
    }
    to {
        opacity: 1;
        transform: translateY(0px);
    }
}

@keyframes fadeDown {
    from {
        opacity : 0;
        transform : translateY(-100%);
    }
    to {
        opacity: 1;
        transform: translateY(0%);
    }
}