@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@800&family=Poppins:wght@400;600;700&display=swap");
.nav-bar {
  z-index: 9999;
  font-family: "Poppins", sans-serif;
  position: absolute;
  top: 0;
  left: 0;
  padding: 1.5rem 0 1.5rem 0;
  backdrop-filter: saturate(180%) blur(2px);
  position: fixed;
  top: 0;
  width: 100%;
}
.nav-bar .container {
  display: flex;
}
.nav-bar .container .logo {
  display: flex;
  align-items: center;
  width: 50%;
}
.nav-bar .container .navbar-buttons {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 50%;
}
.nav-bar .container .navbar-buttons .button {
  padding: 0 1rem 0 1rem;
  margin: 0 2rem 0 2rem;
  min-width: 80px;
}
.nav-bar .container .navbar-buttons .button span a {
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  color: white;
}
.nav-bar .container .navbar-buttons .button span a:hover::after {
  width: 100%;
}
.nav-bar .container .navbar-buttons .button span a::after {
  transition: 0.2s width ease;
  content: "";
  width: 0%;
  height: 1px;
  background-color: white;
}
.nav-bar .container .profile {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  width: 50%;
}
.nav-bar .container .profile span {
  display: flex;
  flex-direction: column;
  color: white;
  position: relative;
  align-items: flex-start;
  padding: 0 1rem 0 1rem;
  margin: 0 2rem 0 2rem;
}
.nav-bar .container .profile span:hover::after {
  width: 100%;
}
.nav-bar .container .profile span::after {
  transition: 0.2s width ease;
  content: "";
  width: 0%;
  height: 1px;
  background-color: white;
}

.landing-page {
  user-select: none;
  font-family: "Poppins", sans-serif;
  position: relative;
  background: url("../static/landing.jpg");
  background-position-y: 90%;
  height: clamp(500px, 100vh, 100vh);
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}
.landing-page .darken {
  position: absolute;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
}
.landing-page .container {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.landing-page .container .wrap-landing-text {
  position: relative;
  display: flex;
  flex-direction: column;
  animation: fadeUp 0.5s ease forwards;
}
.landing-page .container .wrap-landing-text span {
  display: block;
  text-align: center;
  color: white;
}
.landing-page .container .wrap-landing-text .tbch-logo {
  font-size: 3rem;
}
.landing-page .container .wrap-landing-text .tbch-head {
  font-weight: 700;
  font-size: 3rem;
}
.landing-page .container .wrap-landing-text .ctx-logo {
  font-family: "Orbitron", sans-serif;
  margin-top: 2rem;
  font-size: 12px;
}
.landing-page .container .wrap-landing-text .homepage-btn {
  transition: 0.2s background-color ease;
  width: fit-content;
  margin: 0 auto;
  margin-top: 2rem;
  font-family: "Poppins", sans-serif;
  padding: 0.2rem 1rem 0.2rem 1rem;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.1);
}
.landing-page .container .wrap-landing-text .homepage-btn a {
  text-decoration: none;
  color: white;
}
.landing-page .container .wrap-landing-text .homepage-btn:hover {
  background-color: rgb(255, 255, 255);
}
.landing-page .container .wrap-landing-text .homepage-btn:hover a {
  color: black;
}

.wrap-form {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  animation: fadeUp 0.5s ease forwards;
}
.wrap-form form {
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 5px 10px 1px rgb(255, 255, 255);
  border-radius: 15px;
  min-width: 300px;
  max-width: 500px;
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}
.wrap-form form label {
  font-size: 13px;
}
.wrap-form form input {
  margin-bottom: 0.2rem;
}
.wrap-form form .submit-btn {
  font-family: "Poppins", sans-serif;
  border: 1px solid black;
  color: white;
  background-color: #2ecc71;
  margin-top: 2rem;
  border-radius: 5px;
  padding: 0.4rem 0 0.4rem;
  transition: 0.3s background-color ease;
}
.wrap-form form .submit-btn:hover {
  background-color: #5e41a7;
}
.wrap-form form span:nth-of-type(1) i {
  font-size: 12px;
  text-decoration: underline;
}
.wrap-form form span:nth-of-type(2) {
  margin-top: 0.2rem;
  text-decoration: underline;
}
.wrap-form form .message-response {
  color: white;
  border-radius: 5px;
}
.wrap-form form .message-response span {
  display: block;
  font-size: 12px;
  text-align: center;
}

.dash-page {
  user-select: none;
  font-family: "Poppins", sans-serif;
  position: relative;
  background: url("../static/landing.jpg");
  background-position-y: 90%;
  min-height: 100vh;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}
.dash-page .darken {
  position: absolute;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
}
.dash-page .container .main-dashboard {
  animation: fadeUp 0.5s ease forwards;
  padding-top: 7rem;
  padding-bottom: 5rem;
  position: relative;
  display: flex;
  flex-direction: column;
  color: white;
}
.dash-page .container .main-dashboard .dash-title h1 {
  font-size: 65px;
}
.dash-page .container .main-dashboard .your-sharable-link {
  margin-top: 2rem;
}
.dash-page .container .main-dashboard .your-sharable-link span {
  user-select: all;
}
.dash-page .container .main-dashboard .messages {
  margin-top: 3rem;
}
.dash-page .container .main-dashboard .messages h1 {
  font-size: 35px;
  padding: 0 0 1rem 0;
}
.dash-page .container .main-dashboard .messages .messages-main {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 20px;
}
.dash-page .container .main-dashboard .messages .messages-main .message {
  padding: 1rem;
  box-shadow: 0px 2px 3px 1px rgb(255, 255, 255);
  background-color: #353b48;
  border-radius: 5px;
}
.dash-page .container .main-dashboard .messages .messages-main .message h1 {
  text-align: center;
}
.dash-page .container .main-dashboard .messages .messages-main .message span {
  display: block;
  text-align: center;
  word-break: break-all;
}

.message-page {
  user-select: none;
  font-family: "Poppins", sans-serif;
  position: relative;
  background: url("../static/landing.jpg");
  background-position-y: 90%;
  height: clamp(500px, 100vh, 100vh);
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}
.message-page .darken {
  position: absolute;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
}
.message-page .container {
  height: 100%;
}
.message-page .container .main-message {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  color: black;
}
.message-page .container .main-message .message {
  animation: fadeUp 0.5s ease forwards;
  max-width: 200px;
  margin: 0 auto;
  background-color: white;
  box-shadow: 0px 5px 10px 1px rgb(255, 255, 255);
  padding: 0.2rem 1rem 0.2rem 1rem;
  border: 1px solid black;
  border-radius: 5px;
  min-width: 300px;
  text-align: center;
}
.message-page .container .main-message .message h3 {
  font-size: 24px;
}
.message-page .container .main-message .message .inputs .title {
  font-size: 14px;
  word-break: break-all;
}
.message-page .container .main-message .message .inputs form {
  display: flex;
  flex-direction: column;
}
.message-page .container .main-message .message .inputs form .message-input {
  width: 100%;
  min-height: 2rem;
  margin-top: 0.5rem;
  border-radius: 5px;
  border: 1px solid black;
}
.message-page .container .main-message .message .inputs form .submit-btn {
  font-family: "Poppins", sans-serif;
  border: 1px solid black;
  color: white;
  background-color: #2ecc71;
  margin-top: 1rem;
  border-radius: 5px;
  padding: 0.4rem 0 0.4rem;
  transition: 0.3s background-color ease;
}
.message-page .container .main-message .message .inputs form .submit-btn:hover {
  background-color: #5e41a7;
}
.message-page .container .main-message .message .onSuccess {
  display: flex;
  flex-direction: column;
}
.message-page .container .main-message .message .onSuccess .send-anot {
  font-size: 12px;
  font-style: italic;
  text-decoration: underline;
  cursor: pointer;
}
.message-page .container .main-message .message .join {
  display: block;
  padding-top: 1rem;
  cursor: pointer;
  font-size: 12px;
  text-decoration: underline;
}

@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}
@media only screen and (max-width: 992px) {
  .container {
    max-width: 992px;
  }
}
@media only screen and (max-width: 768px) {
  .container {
    max-width: 768px;
  }
  .messages-main {
    grid-template-columns: 1fr 1fr !important;
  }
}
@media only screen and (max-width: 576px) {
  .container {
    max-width: 576px;
  }
  .messages-main {
    grid-template-columns: 1fr !important;
  }
  .bg-src {
    background-position-x: 25%;
  }
  .dash-title h1 {
    font-size: 50px !important;
  }
  .nav-bar .container {
    justify-content: space-between;
  }
  .nav-bar .container .navbar-buttons {
    display: none !important;
  }
  .nav-bar .container .burger-menu-btn {
    display: block !important;
  }
  .profile {
    display: none !important;
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  margin: 0 auto;
  padding: 0 1rem 0 1rem;
}

.nav-bar {
  animation: fadeDown 0.5s cubic-bezier(0, 0.24, 0.32, 1) forwards;
}
.nav-bar .container .burger-menu-btn {
  display: none;
  padding: 0.5rem;
  border-radius: 5px;
}
.nav-bar .container .burger-menu-btn i {
  font-weight: 700;
  font-size: 2rem;
  color: white;
}

.mobile-nav {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 99999;
  height: 100%;
  width: 100%;
  background-color: rgb(0, 0, 0);
  color: white;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  overflow: hidden;
  transition: 0.4s transform cubic-bezier(0, 0.24, 0.32, 1);
  padding: 30px 30px 10px 30px;
}
.mobile-nav .close-button-wrap {
  color: red;
  display: flex;
  width: 100%;
  justify-content: flex-end;
}
.mobile-nav .close-button-wrap i {
  font-size: 25px;
}
.mobile-nav .choices {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
}
.mobile-nav .choices span {
  margin-top: 2rem;
}
.mobile-nav .choices span a {
  color: white;
  text-decoration: none;
  font-family: "Poppins", sans-serif;
  font-size: 20px;
}
.mobile-nav img {
  width: 70px;
  margin-top: 3rem;
}
.mobile-nav .mark {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 2rem;
  height: 100%;
  font-size: 10px;
  user-select: none;
  font-weight: lighter;
  color: rgb(88, 88, 88);
}

@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(2rem);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes fadeDown {
  from {
    opacity: 0;
    transform: translateY(-100%);
  }
  to {
    opacity: 1;
    transform: translateY(0%);
  }
}

