.nav-bar {
    z-index: 9999;
    font-family: "Poppins", sans-serif;
    position: absolute;
    top: 0;
    left: 0;
    padding: 1.5rem 0 1.5rem 0;
    backdrop-filter: saturate(180%) blur(2px);   
    position: fixed;
    top: 0;
    width: 100%;
    .container {
        display: flex;
        .logo {
            display: flex;
            align-items: center;
            width: 50%;
        }
        .navbar-buttons {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            width: 50%;
            .button {
                padding: 0 1rem 0 1rem;
                margin: 0 2rem 0 2rem;
                min-width: 80px;
                span {
                    a {
                        text-decoration: none;
                        text-align: center;
                        cursor: pointer;
                        display: flex;
                        flex-direction: column;
                        color: white;
                        &:hover::after {
                            width: 100%;
                        }
                        &::after {
                            transition: 0.2s width ease;
                            content: "";
                            width: 0%;
                            height: 1px;
                            background-color: white;
                        }
                    }
                }
            }
        }
        .profile {
            display: flex;
            justify-content: center;
            align-items: flex-end;
            flex-direction: column;
            width: 50%;
            span {
                display: flex;
                flex-direction: column;
                color: white;
                position: relative;
                align-items: flex-start;
                padding: 0 1rem 0 1rem;
                margin: 0 2rem 0 2rem;
                &:hover::after {
                    width: 100%;
                }
                &::after {
                    transition: 0.2s width ease;
                    content: "";
                    width: 0%;
                    height: 1px;
                    background-color: white;
                }
            }
        }
    }
}

.landing-page {
    user-select: none;
    font-family: "Poppins", sans-serif;
    position: relative;
    background: url("../static/landing.jpg");
    background-position-y: 90%;
    height: clamp(500px, 100vh, 100vh);
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    .darken {
        position: absolute;
        width: 100%;
        height: 100%;
        min-height: 100vh;
        background-color: rgba(0, 0, 0, 0.6);
    }
    .container {
        display: flex;
        height: 100%;
        align-items: center;
        justify-content: center;
        .wrap-landing-text {
            position: relative;
            display: flex;
            flex-direction: column;
            animation: fadeUp 0.5s ease forwards;
            span {
                display: block;
                text-align: center;
                color: white;
            }
            .tbch-logo {
                font-size: 3rem;
            }
            .tbch-head {
                font-weight: 700;
                font-size: 3rem;
            }
            .ctx-logo {
                font-family: "Orbitron", sans-serif;
                margin-top: 2rem;
                font-size: 12px;
            }
            .homepage-btn {
                transition: 0.2s background-color ease;
                width: fit-content;
                margin: 0 auto;
                margin-top: 2rem;
                font-family: "Poppins", sans-serif;
                padding: 0.2rem 1rem 0.2rem 1rem;
                border-radius: 5px;
                background-color: rgba(255, 255, 255, 0.1);
                a {
                    text-decoration: none;
                    color: white;
                }
            }
            .homepage-btn:hover {
                background-color: rgba(255, 255, 255, 1);
                a {
                    color: black;
                }
            }
        }
    }
}